
import { defineComponent } from "vue";
import { Task } from "../interfaces/Task";
import services from "../services/TaskService";

export default defineComponent({
  data() {
    return {
      task: {} as Task,
    };
  },
  methods: {
    async saveTask() {
      const task = await services.createTask(this.task);
      console.log(task);
      this.$router.push('/');
    },
  },
});
